<template>
  <div class="topList">
    <div class="musicList-top">
      <div class="title">排行榜</div>
      <div class="more">更多 <span class="iconfont icon-you"></span></div>
    </div>
    <div class="swiper-container swiper-topList">
      <div class="swiper-wrapper">
        <router-link
          class="swiper-slide"
          v-for="(item, index) in topListItem"
          :to="{
            path: '/listView',
            query: { id: item.id, scrollValue: scrollValue },
          }"
          :key="index"
        >
          <div class="swiper-Item">
            <div class="title">
              <p>{{ item.name }}</p>
              <span class="iconfont icon-you"></span>
            </div>

            <div
              class="swiper-Item-Item"
              v-for="(item1, index1) in item.item"
              :key="index1"
            >
              <img :src="item1.al.picUrl" />
              <span class="num">{{ index1 + 1 }}</span>
              <div class="author">
                <p>{{ item1.name }}</p>
                <span v-if="item1.ar[0]"
                  >&nbsp;-&nbsp;{{ item1.ar[0].name }}</span
                >
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import Swiper from "swiper"
import "swiper/css/swiper.css"

import { getTopList, getPlaylistDetail } from "@/tools/request"
export default {
  props: ["scrollValue"],
  data() {
    return {
      // 每个排行榜的内容
      topListItem: [],
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.reqRecommend()
      this.initSwiper()
    })
  },
  methods: {
    async reqRecommend() {
      let result = await getTopList()
      let topList = result.data.list.slice(0, 8)
      for (let i = 0; i <= topList.length - 1; i++) {
        let result1 = await getPlaylistDetail(topList[i].id)
        let resultItem = []
        resultItem.id = result1.data.playlist.id
        resultItem.name = result1.data.playlist.name
        resultItem.item = result1.data.playlist.tracks.slice(0, 3)
        this.topListItem.push(resultItem)
      }
    },
    initSwiper() {
      this.swiper = new Swiper(".swiper-topList", {
        slidesPerView: "auto",
        centeredSlides: true,
        // spaceBetween: 30,
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
      })
    },
  },
}
</script>
<style lang="less" scoped>
.topList {
  border-top: 7px solid #f7f7f7;
  width: 7.5rem;
  margin-top: 0.1rem;
  padding-top: 0.2rem;
}
.musicList-top {
  padding: 0 0.2rem;
  overflow: hidden;
  .title {
    font-weight: bold;
    font-size: 0.3rem;
    color: #333;
    float: left;
  }
  .more {
    float: right;
    font-size: 0.1rem;
    border: 1px solid #ccc;
    border-radius: 30px;
    padding: 3px 6px;
    font-weight: bold;
    color: #666;
    .iconfont {
      font-size: 0.1rem;
    }
  }
}
.swiper-container {
  .swiper-slide {
    width: 6.3rem;
    margin: 0.2rem;
    margin-left: 0 !important;
    .swiper-Item {
      width: 6.3rem;
      padding: 0.2rem;
      background: #fff;
      border-radius: 10px;
      box-shadow: 0 0 0.3rem 0 rgba(0, 0, 0, 0.1);
      .title {
        display: flex;
        height: 0.7rem;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        color: #333;
        span {
          margin: 1px 0 0 3px;
        }
      }
      .swiper-Item-Item {
        display: flex;
        align-items: center;
        margin: 0.2rem 0;
        img {
          width: 1rem;
          height: 1rem;
          border-radius: 0.2rem;
        }
        .num {
          font-weight: bold;
          color: #333;
          padding: 0.2rem;
        }
        .author {
          flex: 1;
          display: flex;
          align-items: flex-end;
          p {
            max-width: 3rem;
            white-space: normal;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          span {
            max-width: 3rem;
            font-size: 0.25rem;
            color: #999;
            white-space: normal;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
        }
      }
    }
  }
}
</style>
