<template>
  <div class="iconList">
    <div style="display:flex; ">
      <div
        class="iconItem"
        @click="toView(index)"
        v-for="(item, index) in iconList"
        :key="index"
      >
        <span v-if="index === 0" class="thisDay">{{ thisDay }}</span>
        <img :src="item.iconUrl" alt />
        <p>{{ item.name }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { getBall } from '@/tools/request'

export default {
  data() {
    return {
      iconList: [],
      thisDay: 0,
    }
  },
  async mounted() {
    let result = await getBall()
    this.iconList = result.data.data

    const nowDate = new Date()
    const date = { date: nowDate.getDate() }
    this.thisDay = date.date
  },
  methods: {
    toView(index) {
      if (localStorage.getItem('user') == null) {
        this.$toast('请先登录哦!')
      } else if (index == 0) {
        this.$router.push('/recommend')
      }
    },
  },
}
</script>
<style lang="less" scoped>
.iconList {
  height: 1.7rem;
  margin: 0 0 0.1rem 0;
  display: flex;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
  .iconItem:nth-child(7) img {
    filter: invert(100%) drop-shadow(0 -45px #eb4d44);
  }
  .iconItem:nth-child(8) img {
    filter: invert(100%) drop-shadow(0 -45px #eb4d44);
  }
  .iconItem {
    position: relative;
    width: 1rem;
    height: 1rem;
    background-color: #fdf5f5;
    margin: 0.2rem;
    border-radius: 50%;
    z-index: 0;
    img {
      position: absolute;
      top: 45px;
      left: 0;
      height: 100%;
      filter: drop-shadow(0 -45px #eb4d44);
      z-index: -1;
    }
    p {
      font-size: 0.1rem;
      text-align: center;
      margin-top: 1.04rem;
      color: rgb(109, 109, 109);
    }
  }
  .iconItem:first-child p {
    margin-top: 0;
  }
  .thisDay {
    display: flex;
    width: 100%;
    height: 104%;
    align-items: center;
    justify-content: center;
    font-size: 0.22rem;
    color: #eee;
  }
}
</style>
