<template>
  <div class="swiper-container swiper-swiper">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(item, index) in imgs" :key="index">
        <div>
          <img :src="item.pic" alt="" />
        </div>
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
  </div>
</template>
<script>
import Swiper from "swiper"
import "swiper/css/swiper.css"
import { getBanner } from "@/tools/request"

export default {
  data() {
    return {
      imgs: [],
    }
  },
  mounted() {
    setTimeout(() => {
      this.initSwiper()
    }, 300)
    this.getData()
  },
  methods: {
    initSwiper() {
      if (this.swiper != null) return
      this.swiper = new Swiper(".swiper-swiper", {
        loop: true, // 循环模式选项
        autoplay: {
          // 自动播放
          delay: 3000, // 自动播放时间
          disableOnInteraction: false, // 用户操作swiper之后，是否禁用autoplay
        },
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper

        // 如果需要分页器
        pagination: {
          el: ".swiper-pagination",
        },
      })
    },
    async getData() {
      let result = await getBanner(2)
      this.imgs = result.data.banners
      this.$nextTick(() => {
        this.initSwiper()
      })
      // setTimeout(() => {
      //   this.initSwiper()
      // }, 0)
    },
  },
}
</script>
<style lang="less" scoped>
.swiper-container {
  width: 7.5rem;
  height: auto;
  .swiper-slide {
    height: 3rem;
    div {
      padding: 0.2rem;
    }
    img {
      width: 100%;
      border-radius: 0.2rem;
    }
  }
}
/deep/.swiper-pagination-bullet-active {
  background-color: red;
}
</style>
