<template>
  <div class="fontMusicList">
    <div class="musicList-top">
      <div class="title">推荐歌单</div>
      <div class="more">更多 <span class="iconfont icon-you"></span></div>
    </div>
    <div class="musicList">
      <router-link
        class="musicList-item"
        v-for="(item, index) in musicList.value"
        :key="index"
        :to="{
          path: '/listView',
          query: { id: item.id, scrollValue: scrollValue },
        }"
      >
        <div class="playNum">
          <span class="iconfont icon-bofang101"></span>
          <span>{{ changePlayCount(item.playCount) }}</span>
        </div>
        <img :src="item.picUrl" alt="" />
        <span class="context">{{ item.name }}</span>
      </router-link>
    </div>
  </div>
</template>
<script>
import { getPersonalList } from "@/tools/request"
import { reactive, onMounted } from "vue"

export default {
  props: ["scrollValue"],
  setup() {
    let musicList = reactive([])
    function changePlayCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(0) + "亿"
      } else if (num >= 10000) {
        return (num / 10000).toFixed(0) + "万"
      } else {
        return num
      }
    }
    onMounted(async () => {
      let result = await getPersonalList()
      musicList.value = result.data.result
    })
    return {
      musicList,
      changePlayCount,
    }
  },
}
</script>
<style lang="less" scoped>
.fontMusicList {
  border-top: 1px solid rgb(235, 235, 235);
  padding-top: 0.2rem;
}
.musicList-top {
  padding: 0 0.2rem;
  overflow: hidden;
  .title {
    font-weight: bold;
    font-size: 0.3rem;
    color: #333;
    float: left;
  }
  .more {
    float: right;
    font-size: 0.1rem;
    border: 1px solid #ccc;
    border-radius: 30px;
    padding: 3px 6px;
    font-weight: bold;
    color: #666;
    .iconfont {
      font-size: 0.1rem;
    }
  }
}
.musicList {
  display: flex;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: visible;
  &::-webkit-scrollbar {
    display: none;
  }
  .musicList-item:last-child {
    margin: 0.2rem !important;
  }
  .musicList-item {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 0.2rem 0 0.2rem 0.2rem;
    width: 2rem;
    img {
      width: 2rem;
      height: 2rem;
      border-radius: 0.2rem;
      margin-bottom: 0.1rem;
    }
    .context {
      width: 2rem;
      height: 0.6rem;
      font-size: 0.1rem;
      letter-spacing: 1px;
      white-space: normal;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .playNum {
      position: absolute;
      right: 4px;
      top: 4px;
      color: #fff;
      letter-spacing: 1px;
      padding: 0 8px;
      background: rgba(255, 255, 255, 0.185);
      border-radius: 0.15rem;
      span {
        font-size: 10px;
      }
    }
  }
}
</style>
