<template>
  <div class="home">
    <top-nav />
    <swiper />
    <icon-list />
    <font-music-list :scrollValue="scrollValue" />
    <top-list :scrollValue="scrollValue" />
    <tab-bar />
  </div>
</template>

<script>
import topNav from "@/components/Home/topNav"
import swiper from "@/components/Home/swiper"
import iconList from "@/components/Home/iconList"
import fontMusicList from "@/components/Home/fontMusicList"
import topList from "@/components/Home/topList"
import tabBar from "@/components/common/tabBar.vue"

export default {
  name: "Home",
  components: {
    topNav,
    swiper,
    iconList,
    fontMusicList,
    topList,
    tabBar,
  },
  data() {
    return {
      scrollValue: 0,
    }
  },
  activated() {
    setTimeout(() => {
      document.body.scrollTop = this.scrollValue
      document.documentElement.scrollTop = this.scrollValue
      window.addEventListener("scroll", this.handleScroll, true)
    }, 200)
  },
  deactivated() {
    window.removeEventListener("scroll", this.handleScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll, true)
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("scroll", this.handleScroll, true)
    })
  },
  methods: {
    handleScroll() {
      this.scrollValue =
        document.documentElement && document.documentElement.scrollTop
    },
  },
}
</script>
<style lang="less" scoped>
.home {
  width: 7.5rem;
  // height: 100vh;
  background: #fff;
  background-image: linear-gradient(#e7e7e7, #fff, #fff, #fff);
  padding-bottom: 2rem;
}
</style>
